.page-hero {
  padding-top: 153px;
  padding-bottom: 80px;
  min-height: 600px;
  background-color: var(--color-athens);
  background-position: right -8px top;
  background-repeat: no-repeat;
  background-color: var(--color-athens);

  &__breadcrumbs {
    margin-bottom: 40px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    max-width: 550px;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--color-ebony);

    &--small {
      font-size: 64px;
      line-height: 120%;
      letter-spacing: 1px;
      text-transform: initial;
      color: #1E212C;
    }
    &--mb {
      margin-bottom: 60px;
    }
  }
  &__descr {
    margin: 0;
    max-width: 600px;
    font-size: 20px;
    line-height: 150%;
    color: var(--color-storm);
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
  }
  &__meta {
    .article-meta__item {
      font-size: 16px;
      line-height: 160%;
    }
  a.article-meta__item {
    font-weight: 700;
        color: var(--color-primary);

  }
  time.article-meta__item {
    padding-left: 28px;
    background-image: url("../img/time.svg");
    background-position: left center;
    background-size: 20px 20px;
    background-repeat: no-repeat;
  }
  .article-meta__item--comments {
    background-size: 20px 20px;
  }
  .article-meta__item:not(:last-child)::after {
    right: -17px;
    height: 16px;
  }

  .article-meta__item:not(:last-child){
    margin-right: 25px;
  }

}

 .page-hero__social {
    svg {
      fill: var(--color-storm);
      opacity: 1;
    }
  }
  &--services {
    background-image: url("../img/services-hero-bg.png");

  }
  &--services-inner {
    background-image: url("../img/services-inner-bg.png");

  }
  &--work {
    background-image: url("../img/work-hero-bg.png");

  }
  &--work-inner {
    background-image: url("../img/work-hero-inner-bg.png");
    background-position: right -8px top 48px;
    background-color: var(--color-light);

  }
  &--about {
    background-image: url("../img/about-hero-bg.png");
    //background-position: right -8px top 48px;
    background-color: var(--color-light);

  }
  &--positions {
    background-image: url("../img/position-hero-bg.png");
    background-position: right -9px bottom;
    //background-position: right -8px top 48px;
    background-color: var(--color-light);

  }
  &--news {
    background-image: url("../img/news-hero-bg.png");
    background-position: right -9px bottom;
    background-color: #F4F5F7;;

  }
  &--post {
    .page-hero__title {
      margin-bottom: 48px;
      max-width: none;
      font-weight: 700;
      font-size: 64px;
      line-height: 120%;
      letter-spacing: 1px;
      text-transform: initial;
      color: var(--color-ebony);
    }

   // background-color: var(--color-storm);

  }
}
