.form-field {
  @include flex-column-start;

  &__caption {
    margin-bottom: 7px;
    font-size: 14px;
    line-height: 150%;
    color: var(--color-gray);

  }
  &__input {
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding-left: 15px;
    width: 260px;
    height: 44px;
    font-size: 14px;
    line-height: 150%;


    &::placeholder {
      color: rgba(154, 156, 165, 1);
    }
    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }


  }
  &--big {
    flex-grow: 1;

    .form-field__input {
      width: 100%;
    }
  }

  &--gray {
    width: 100%;

    .form-field__input {
      width: 100%;
      background-color: var(--color-athens);
    }
  }
}
